import styled from 'styled-components';

export const Text = styled.div`
    font-family: sans-serif;
    position: relative;
    color: #777;
    margin-top: 20px;
    font-weight: 700;
    font-size: 18px;
    letter-spacing: 1px;
    text-transform: uppercase;
    transition: 0.5s;
`;

export const Card = styled.div`
    position: relative;
    width: 250px;
    background: linear-gradient(0deg,#1b1b1b,#222,#1b1b1b);
    display: flex;
    margin: 10px;
    justify-content: center;
    align-items: center;
    height: 300px;
    border-radius: 4px;
    text-align: center;
    overflow: hidden;
    transition: 0.5s;
    &:hover{
        transform: translateY(-10px);
        box-shadow: 0 15px 35px rgba(0, 0, 0, 0.5);
        h2{
            color: #fff;
            font-size: 60px;
        }
        span{
            color: #fff;
        }
        ${Text}{
            color: #fff;
        }
    };

    &:before{
        content: '';
        position: absolute;
        top: 0;
        left: -50%;
        width: 100%;
        height: 100%;
        background: rgba(255,255,255,0.03);
        pointer-events: none;
        z-index: 1;
    };
`;

export const Percent = styled.div`
    position: relative;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    box-shadow: inset 0 0 50px #000;
    background: #222;
    z-index: 1000;
`;

export const Number = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    h2{
        font-family: sans-serif;
        color: #777;
        font-weight: 700;
        font-size: 40px;
        transition: 0.5s;
        span{
            font-size: 24px;
            color: #777;
            transition: 0.5s;
        }
    }

`;

export const Svg = styled.svg`
    position: relative;
    width: 150px;
    height: 150px;
    z-index: 1000;
    circle:nth-child(2){
        stroke-dasharray: 440;
        stroke-dashoffset: calc(440 - (440 * ${prop => prop.offset}) /100);
        stroke: ${prop => prop.name};
    }
`;

export const Circle = styled.circle`
    widows: 100%;
    height: 100%;
    fill: none;
    stroke: #191919;
    stroke-width: 10;
    stroke-linecap: round;
    transform: translate(5px, 5px);
`;
