import React from 'react';

import { Card, Percent, Svg, Circle, Number, Text } from './styles';

import { CardProgressProps } from './types';

import { COLOR } from './colors';

const CardProgress: React.FC<CardProgressProps> = (props) => {
    return (
        <Card>
            <div>
                <Percent>
                    <Svg name={COLOR[props.language]} offset={props.percent}>
                        <Circle cx="70" cy="70" r="70"></Circle>
                        <Circle cx="70" cy="70" r="70"></Circle>
                    </Svg>
                    <Number>
                        <h2>{props.percent}<span>%</span></h2>
                    </Number>
                </Percent>
                <Text>{props.language}</Text>
            </div>
        </Card>
    );
}

CardProgress.defaultProps = {
    language: 'JAVASCRIPT',
    percent: 0
}

export default CardProgress;